@font-face {
    font-family: 'digicon';
    src: url('../../assets/fonts/digicon.eot?f408f0');
    src: url('../../assets/fonts/digicon.eot?f408f0#iefix') format('embedded-opentype'),
        url('../../assets/fonts/digicon.ttf?f408f0') format('truetype'),
        url('../../assets/fonts/digicon.woff?f408f0') format('woff'),
        url('../../assets/fonts/digicon.svg?f408f0#digicon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^='icon-'],
[class*=' icon-'] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'digicon' !important;
    speak: none;
    font-size: 90px;
    color: #fff;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    display: inline-block;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

